// Level thresholds (XP required for each level)
export const LEVEL_THRESHOLDS = [
  0, // Level 1
  100, // Level 2
  250, // Level 3
  500, // Level 4
  1000, // Level 5
  2000, // Level 6
  3500, // Level 7
  5500, // Level 8
  8000, // Level 9
  11000, // Level 10
];

/**
 * Calculate level from XP
 */
function calculateLevel(xp: number): number {
  let level = 1;
  for (let i = 0; i < LEVEL_THRESHOLDS.length; i++) {
    if (xp >= LEVEL_THRESHOLDS[i]) {
      level = i + 1;
    } else {
      break;
    }
  }
  return level;
}

/**
 * Calculate XP for next level
 */
function calculateXpForNextLevel(currentXp: number): number {
  const currentLevel = calculateLevel(currentXp);
  if (currentLevel >= LEVEL_THRESHOLDS.length) {
    return 0; // Max level reached
  }
  return LEVEL_THRESHOLDS[currentLevel] - currentXp;
}

/**
 * Award XP to user
 */
export async function awardXP(
  userId: string,
  amount: number,
  reason: string
): Promise<{ newXP: number; newLevel: number; leveledUp: boolean }> {
  const startTime = Date.now();

  // Get user stats
  const statsSnapshot = await db
    .collection(COLLECTIONS.USER_STATS)
    .where('userId', '==', userId)
    .limit(1)
    .get();

  let statsDoc;
  let currentXP = 0;
  let currentLevel = 1;

  if (statsSnapshot.empty) {
    // Create new stats document
    const newStats: UserStats = {
      userId,
      totalInvoiced: 0,
      totalCollected: 0,
      totalInvoices: 0,
      averagePaymentDays: 0,
      onTimePercentage: 100,
      streak: 0,
      badges: [],
      level: calculateLevel(amount),
      rank: 0,
      gamificationXP: amount,
      gamificationLevel: calculateLevel(amount),
      currentStreak: 0,
      longestStreak: 0,
      achievements: [],
      daysActivePastMonth: 0,
      sessionsThisMonth: 0,
      avgSessionDuration: 0,
      churnRiskScore: 0,
      engagementLevel: 'medium',
      calculatedAt: Timestamp.now(),
    };

    const docRef = await db.collection(COLLECTIONS.USER_STATS).add(newStats);
    statsDoc = docRef;
    currentXP = amount;
    currentLevel = newStats.level;
  } else {
    statsDoc = statsSnapshot.docs[0].ref;
    const stats = statsSnapshot.docs[0].data() as UserStats;
    currentXP = stats.gamificationXP || 0;
    currentLevel = stats.gamificationLevel || 1;

    const newXP = currentXP + amount;
    const newLevel = calculateLevel(newXP);

    await statsDoc.update({
      gamificationXP: newXP,
      gamificationLevel: newLevel,
      updatedAt: Timestamp.now(),
    });

    currentXP = newXP;
    currentLevel = newLevel;
  }

  const previousLevel = calculateLevel(currentXP - amount);
  const leveledUp = currentLevel > previousLevel;

  logDbOperation('award_xp', COLLECTIONS.USER_STATS, userId, Date.now() - startTime);

  logInfo('Awarded XP to user', { userId, amount, reason, level: currentLevel });

  return {
    newXP: currentXP,
    newLevel: currentLevel,
    leveledUp,
  };
}

/**
 * Check and award badges
 */
export async function checkAndAwardBadges(userId: string): Promise<string[]> {
  const startTime = Date.now();
  const newBadges: string[] = [];

  // Get user stats
  const statsSnapshot = await db
    .collection(COLLECTIONS.USER_STATS)
    .where('userId', '==', userId)
    .limit(1)
    .get();

  if (statsSnapshot.empty) {
    return [];
  }

  const stats = statsSnapshot.docs[0].data() as UserStats;
  const currentBadges = stats.badges || [];

  // Aliases for backward compatibility
  const totalPaid = stats.totalCollected;
  const totalRevenue = stats.totalInvoiced;
  const currentStreak = stats.streak;

  // Check each badge
  // First Invoice
  if (!currentBadges.includes(BADGES.FIRST_INVOICE.id) && stats.totalInvoices >= 1) {
    newBadges.push(BADGES.FIRST_INVOICE.id);
    await awardXP(userId, 10, 'Earned badge: First Invoice');
  }

  // Invoice Master 5
  if (!currentBadges.includes(BADGES.INVOICE_MASTER_5.id) && stats.totalInvoices >= 5) {
    newBadges.push(BADGES.INVOICE_MASTER_5.id);
    await awardXP(userId, 25, 'Earned badge: Invoice Master');
  }

  // Invoice Master 10
  if (!currentBadges.includes(BADGES.INVOICE_MASTER_10.id) && stats.totalInvoices >= 10) {
    newBadges.push(BADGES.INVOICE_MASTER_10.id);
    await awardXP(userId, 50, 'Earned badge: Invoice Expert');
  }

  // First Payment
  if (!currentBadges.includes(BADGES.FIRST_PAYMENT.id) && totalPaid >= 1) {
    newBadges.push(BADGES.FIRST_PAYMENT.id);
    await awardXP(userId, 20, 'Earned badge: First Payment');
  }

  // Payment Streak 7
  if (!currentBadges.includes(BADGES.PAYMENT_STREAK_7.id) && currentStreak >= 7) {
    newBadges.push(BADGES.PAYMENT_STREAK_7.id);
    await awardXP(userId, 50, 'Earned badge: Week Streak');
  }

  // Payment Streak 30
  if (!currentBadges.includes(BADGES.PAYMENT_STREAK_30.id) && currentStreak >= 30) {
    newBadges.push(BADGES.PAYMENT_STREAK_30.id);
    await awardXP(userId, 100, 'Earned badge: Month Streak');
  }

  // Revenue 1K
  if (!currentBadges.includes(BADGES.REVENUE_1K.id) && totalRevenue >= 1000) {
    newBadges.push(BADGES.REVENUE_1K.id);
    await awardXP(userId, 75, 'Earned badge: ¬£1K Revenue');
  }

  // Revenue 10K
  if (!currentBadges.includes(BADGES.REVENUE_10K.id) && totalRevenue >= 10000) {
    newBadges.push(BADGES.REVENUE_10K.id);
    await awardXP(userId, 200, 'Earned badge: ¬£10K Revenue');
  }

  // Update badges in stats
  if (newBadges.length > 0) {
    await statsSnapshot.docs[0].ref.update({
      badges: [...currentBadges, ...newBadges],
      updatedAt: Timestamp.now(),
    });
  }

  logDbOperation('check_badges', COLLECTIONS.USER_STATS, userId, Date.now() - startTime);

  return newBadges;
}

/**
 * Update payment streak
 */
export async function updatePaymentStreak(userId: string, paymentDate: Date): Promise<number> {
  const startTime = Date.now();

  // Get user stats
  const statsSnapshot = await db
    .collection(COLLECTIONS.USER_STATS)
    .where('userId', '==', userId)
    .limit(1)
    .get();

  if (statsSnapshot.empty) {
    return 0;
  }

  const stats = statsSnapshot.docs[0].data() as UserStats;
  const lastStreakDate = stats.lastStreakDate?.toDate();
  let currentStreak = stats.currentStreak || 0;
  const longestStreak = stats.longestStreak || 0;

  if (!lastStreakDate) {
    // First payment
    currentStreak = 1;
  } else {
    const daysDiff = Math.floor(
      (paymentDate.getTime() - lastStreakDate.getTime()) / (1000 * 60 * 60 * 24)
    );

    if (daysDiff === 1) {
      // Consecutive day
      currentStreak += 1;
    } else if (daysDiff === 0) {
      // Same day, don't change streak
    } else {
      // Streak broken
      currentStreak = 1;
    }
  }

  // Update streak
  await statsSnapshot.docs[0].ref.update({
    currentStreak,
    longestStreak: Math.max(currentStreak, longestStreak),
    lastStreakDate: Timestamp.fromDate(paymentDate),
    updatedAt: Timestamp.now(),
  });

  // Award XP for streak milestones
  if (currentStreak === 7) {
    await awardXP(userId, 30, '7-day payment streak');
  } else if (currentStreak === 30) {
    await awardXP(userId, 100, '30-day payment streak');
  } else if (currentStreak % 10 === 0 && currentStreak > 0) {
    await awardXP(userId, 20, `${currentStreak}-day payment streak`);
  }

  logDbOperation('update_streak', COLLECTIONS.USER_STATS, userId, Date.now() - startTime);

  return currentStreak;
}

/**
 * Get user gamification stats
 */
export async function getGamificationStats(userId: string): Promise<{
  xp: number;
  level: number;
  xpForNextLevel: number;
  badges: string[];
  currentStreak: number;
  longestStreak: number;
}> {
  const startTime = Date.now();

  const statsSnapshot = await db
    .collection(COLLECTIONS.USER_STATS)
    .where('userId', '==', userId)
    .limit(1)
    .get();

  if (statsSnapshot.empty) {
    return {
      xp: 0,
      level: 1,
      xpForNextLevel: LEVEL_THRESHOLDS[1],
      badges: [],
      currentStreak: 0,
      longestStreak: 0,
    };
  }

  const stats = statsSnapshot.docs[0].data() as UserStats;

  logDbOperation('get_gamification_stats', COLLECTIONS.USER_STATS, userId, Date.now() - startTime);

  return {
    xp: stats.gamificationXP || 0,
    level: stats.gamificationLevel || 1,
    xpForNextLevel: calculateXpForNextLevel(stats.gamificationXP || 0),
    badges: stats.badges || [],
    currentStreak: stats.currentStreak || 0,
    longestStreak: stats.longestStreak || 0,
  };
}

/**
 * Get leaderboard (top users by XP)
 */
export async function getLeaderboard(limit = 10): Promise<Array<{
  userId: string;
  xp: number;
  level: number;
  badges: string[];
}>> {
  const startTime = Date.now();

  const snapshot = await db
    .collection(COLLECTIONS.USER_STATS)
    .orderBy('gamificationXP', 'desc')
    .limit(limit)
    .get();

  logDbOperation('get_leaderboard', COLLECTIONS.USER_STATS, undefined, Date.now() - startTime);

  return snapshot.docs.map((doc) => {
    const stats = doc.data() as UserStats;
    return {
      userId: stats.userId,
      xp: stats.gamificationXP || 0,
      level: stats.gamificationLevel || 1,
      badges: stats.badges || [],
    };
  });
}
