import { NextRequest, NextResponse } from 'next/server';
import { getLeaderboard } from '@/services/gamificationService';
import { handleApiError } from '@/utils/error';
import { logApiRequest, logApiResponse } from '@/utils/logger';

export const dynamic = 'force-dynamic';

/**
 * Get leaderboard
 * GET /api/gamification/leaderboard?limit=10
 */
export async function GET(req: NextRequest): Promise<NextResponse> {
  const startTime = Date.now();

  try {
    logApiRequest('GET', '/api/gamification/leaderboard');

    // Parse query params
    const { searchParams } = new URL(req.url);
    const limit = parseInt(searchParams.get('limit') || '10', 10);

    // Get leaderboard
    const leaderboard = await getLeaderboard(limit);

    // Log response
    const duration = Date.now() - startTime;
    logApiResponse('GET', '/api/gamification/leaderboard', 200, duration);

    return NextResponse.json({
      leaderboard,
      total: leaderboard.length,
    });
  } catch (error) {
    const duration = Date.now() - startTime;
    logApiResponse('GET', '/api/gamification/leaderboard', 500, duration);
    return handleApiError(error);
  }
}
